@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
.schedule-container {
    background: #337ab7;
    color: #ffffff;
}
.dx-tooltip-appointment-item-content-date{
    color: #EE5A24;
}
.dx-tooltip-appointment-item-delete-button-container{
    display: none;
}
.dx-overlay-content.dx-resizable {
    top: -60%!important;
    width: 50%!important;
}
.dx-scheduler .dx-scheduler-overlay-panel .dx-overlay-content {
    position: relative;
    background: #ffffff;
    box-shadow: 3px 4px 6px #718093, -3px -4px 6px #71809350!important;
}
.schedule-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 1rem;
}
.icon-account {
    display: flex;
    align-items: center;
}
.icon-account > p {
    font-size: 1.7rem;
}
.icon-account-avatar > img {
    width: 5rem;
    box-shadow: 3px 4px 4px rgba(0, 0, 0, 0.541);
    border-radius: 50%;
}
.schedule-title {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    padding: 2rem;
}
.schedule-title > svg {
    width: 5rem;
    height: 5rem;
    margin-right: 0.5rem;
}
.schedule-title > p {
    font-size: 2.4rem;
}
.MuiMenuItem-root {
    font-size: 1.8rem !important;
}
.dx-scheduler.dx-widget.dx-visibility-change-handler {
    height: 90rem;
    width: 80%;
    margin: 2rem auto;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.281), -5px 5px 5px rgba(0, 0, 0, 0.281);
}

@media (max-width: 1024px) {
    .dx-scheduler.dx-widget.dx-visibility-change-handler {
        width: 95%;
    }
    .dx-overlay-content.dx-resizable {
        top: -50%!important;
        width: 60%!important;
    }
}
@media (max-width: 768px) {
    .dx-overlay-content.dx-resizable {
        width: 100%!important;
    }
    .schedule-title {
        padding: 1.5rem;
    }
    .schedule-title > p {
        font-size: 2.1rem;
    }
    .icon-account > p {
        font-size: 1.7rem;
        text-align: end;
    }
    .schedule-title > svg {
        width: 4rem;
        height: 4rem;
        margin-right: 0.5rem;
    }
}
@media (max-width: 500px) {
    .dx-overlay-content.dx-resizable {
        width: 100%!important;
        top: -40%!important;
    }
    .dx-tooltip-appointment-item-content > div{
        font-size: 1.6rem!important;
    }
    .dx-tooltip-appointment-item{
        margin-left: 0!important;
    }
    .dx-item-content.dx-list-item-content{
        padding-left: 0!important
    }
}
@media (max-width: 425px) {
    .dx-overlay-content.dx-resizable {
        width: 100%!important;
        top: -40%!important;
    }
    .dx-tooltip-appointment-item-content > div{
        font-size: 1.5rem!important;
    }
    .dx-tooltip-appointment-item{
        margin-left: 0!important;
    }
    .dx-tooltip-appointment-item-marker{
        display: none;
    }
    .dx-item-content.dx-list-item-content{
        padding-left: 0!important
    }
    .schedule-title {
        padding: 1rem;
    }
    .schedule-title > p {
        font-size: 1.5rem;
    }
    .icon-account > p {
        font-size: 1.3rem;
    }
    .icon-account-avatar > img {
        width: 3rem;
    }
    .schedule-title > svg {
        width: 3rem;
        height: 3rem;
        margin-right: 0.5rem;
    }
}
@media (max-width: 350px) {
    .dx-overlay-content.dx-resizable {
        width: 100%!important;
        top: -40%!important;
    }
    .dx-tooltip-appointment-item-content > div{
        font-size: 1.3rem!important;
    }
}


.background {
    background-image: url(/schedule/static/media/background.e24c884b.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
}

/* FORM */
.login {
    position: absolute;
    width: 50rem;
    top: 50%;
    left: 10%;
    -webkit-transform: translate(-10%,-50%);
            transform: translate(-10%,-50%);
    padding: 4rem;
    background: rgba(255, 255, 255, 0.842);
    color: #474444;
    border-radius: 2rem;
    box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.562);
}
.login > h1 {
    padding-bottom: 3rem;
    text-align: center;
    font-size: 3rem;
}
.login-form > div > div {
    width: 100%;
    margin-bottom: 2rem;
}
.login-form label {
    font-size: 1.8rem;
    background: #ddd8e4;
    padding-right: 0.5rem;
}
.login-form input {
    font-size: 1.8rem;
}
.login-form button{
    padding: 1rem;
    font-size: 1.4rem;
    float: right;
    width: 30%;
}
.login-form-account, .login-form-password{
    position: relative;
}
span.login-form-icon{
    position: absolute;
    right: 1rem;
    top: 20%;
    padding-left: 1rem;
    border-left: .5px solid rgb(175, 173, 173);
    color: rgb(92, 84, 84);
    cursor: pointer;
}
span.login-form-icon > svg{
    font-size: 3rem;
}

@media (max-width: 768px) {
    .login{
        width: 90%;
        left: 14.5%;
    }
    .login-form button{
        width: 15rem;
    }
    
}
@media (max-width: 425px) {
    .login{
        width: 90%;
    }
    .login > h1{
        font-size: 2rem;
    }
    .login-form button{
        width: 100%;
    }
    
}


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    font-size: 62.5%;
}
body{
    font-family: 'Quicksand', sans-serif;
}
/* ======LOADDING =========== */
.loadding {
    position: absolute;
    background: #00000085;
    width: 100%;
    height: 100%;
    display: flex;
    z-index: 1;
    justify-content: center;
    align-items: center;
}
.loadding .MuiCircularProgress-colorPrimary{
    color: #ffffff;
    width: 6rem!important;
    height: 6rem!important;
}


/* ===========ALERT 2========= */

.swal2-popup.swal2-toast{
    padding: 1.7rem;
    font-size: 1.6rem;
    box-shadow: none;
}

